* {
  text-decoration: none;
  color: rgb(88, 88, 88);
}

.fas {
  color: rgb(87, 92, 200);
}


/* < Alert > */
.alert {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
  padding: 0.6rem;
  font-size: 2rem;
  border: 2px solid;
  border-radius: 10px;
}
.alert-danger {
  color: #dc3545;
  border-color: #dc3545;
}
.alert-success {
  color: #28a745;
  border-color: #28a745;
}
/* </ Alert > */


/* < Layout > */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title {
  font-size: 4.5rem;
  margin: 0;
}
.menu {
  font-size: 2.5rem;
}
/* </ Layout > */

/* < ToDo > */
.todo-content {
  width: 88%;
  margin: 0;
}
.todo-btn{
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(87, 92, 200);
}
.plus-icon {
  text-shadow: 0 0 1.5px #333;
}
.plus-icon:hover {
  text-shadow: none;
}
.todo-item {
  box-shadow:0 10px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
  padding: 2rem;
  border-radius: 5px;
  margin-bottom: 2rem;
}
.todo-info {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: stretch;
}
.info-1 {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
.todo-date{
  width: 50%;
  margin: 0;
}
.todo-loc {
  width: 30%;
  margin: 0;
}
.todo-del {
  background-color: #dc3545;
  color: #fff;
  text-align: center;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  margin: 0;
  border-radius: 5px;
  width: 5%;
}
.todo-del:hover {
  background-color: #d42335;
}
.todo-submit {
  color: #fff;
  text-align: center;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  border-radius: 5px;
  width: 5%;
  height: 100%;
  background-color: #28a745;
}
.todo-submit:hover {
  background-color: #108f2e;
}
/* </ ToDo > */

/* < Profile > */
.profile {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.profile > img {
  width: 25%;
  height: 25%;
  margin: 0 auto;
}
.logging {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
/* </ Profile > */


/* < Landing > */
.quote {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  top: 25vh;
}

.quote > * {
  text-align: center;
}
/* </ Landing > */


/* mobile */
@media (max-width: 767px) {
  .title {
    font-size: 4rem;
  }
  .menu {
    font-size: 1.8rem;
  }
  .quote {
    top: 20vh;
  }
  .todo-item {
    padding: 1rem;
  }
  .profile > img {
    width: 45%;
    height: 45%;
  }
}